<template>
  <div class="next-train">
    <div id="form">
      <div>
        <b-container class="bv-example-row">
          <b-row>
            <b-col>
              <b-form-select id="selectedLine" v-model="selectedLine" :options="lineOptions" @input="updateStations" />
            </b-col>
            <b-col>
              <b-form-select id="selectedStation" v-model="selectedStation" :options="stationOptions" @input="refresh" />
            </b-col>
          </b-row>
        </b-container>
      </div>
    </div>
    <br/>
    <div class="result">
      <h1>{{codeToText(line)}}</h1>
      <h2>{{codeToText(sta)}}</h2>
      <h3>現在時間: {{currentTime}}</h3>
      
      <h3>往{{uniqueUpDestinations}}</h3>
      <br/>
      <div>
        <b-table striped hover
          ref="table"
          :items="myProvider"
          :fields="fields">
        </b-table>
      </div>
      
      <h3>往{{uniqueDownDestinations}}</h3>
      <br/>
      <div>
        <b-table striped hover
          ref="table2"
          :items="myProvider2"
          :fields="fields">
        </b-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NextTrain',
  data() {
    return {
      lineOptions: [
        { value:'AEL', text:this.codeToText('AEL') },
        { value:'TCL', text:this.codeToText('TCL') },
        { value:'WRL', text:this.codeToText('WRL') },
        { value:'TKL', text:this.codeToText('TKL') },
      ],
      aelStations: [
          { value:'HOK', text:this.codeToText('HOK') },
          { value:'KOW', text:this.codeToText('KOW') },
          { value:'TSY', text:this.codeToText('TSY') },
          { value:'AIR', text:this.codeToText('AIR') },
          { value:'AWE', text:this.codeToText('AWE') }
      ],
      tclStations: [
          { value:'HOK', text:this.codeToText('HOK') },
          { value:'KOW', text:this.codeToText('KOW') },
          { value:'OLY', text:this.codeToText('OLY') },
          { value:'NAC', text:this.codeToText('NAC') },
          { value:'LAK', text:this.codeToText('LAK') },
          { value:'TSY', text:this.codeToText('TSY') },
          { value:'SUN', text:this.codeToText('SUN') },
          { value:'TUC', text:this.codeToText('TUC') }
      ],
      wrlStations: [
          { value:'HUH', text:this.codeToText('HUH') },
          { value:'ETS', text:this.codeToText('ETS') },
          { value:'AUS', text:this.codeToText('AUS') },
          { value:'NAC', text:this.codeToText('NAC') },
          { value:'MEF', text:this.codeToText('MEF') },
          { value:'TWW', text:this.codeToText('TWW') },
          { value:'KSR', text:this.codeToText('KSR') },
          { value:'YUL', text:this.codeToText('YUL') },
          { value:'LOP', text:this.codeToText('LOP') },
          { value:'TIS', text:this.codeToText('TIS') },
          { value:'SIH', text:this.codeToText('SIH') },
          { value:'TUM', text:this.codeToText('TUM') }
      ],
      tklStations: [
          { value:'NOP', text:this.codeToText('NOP') },
          { value:'QUB', text:this.codeToText('QUB') },
          { value:'YAT', text:this.codeToText('YAT') },
          { value:'TIK', text:this.codeToText('TIK') },
          { value:'TKO', text:this.codeToText('TKO') },
          { value:'LHP', text:this.codeToText('LHP') },
          { value:'HAH', text:this.codeToText('HAH') },
          { value:'POA', text:this.codeToText('POA') }
      ],
      stationOptions: [],
      selectedLine: 'WRL',
      selectedStation: 'TWW',
      results: [],
      currentTime: '',
      fields: [
        {
          key: 'dest',
          label: '目的地'
        },
        {
          key: 'ttnt',
          label: '預計到達'
        },
        {
          key: 'time',
          label: '到達時間'
        },
        {
          key: 'plat',
          label: '月台'
        }
      ],
      line: 'WRL',
      sta: 'TWW',
      allUpDestinations: [],
      allDownDestinations: [],
      uniqueUpDestinations: '',
      uniqueDownDestinations: ''
    }
  },
  methods: {
    updateStations() {
      console.log('line changed')
      if (this.selectedLine == 'AEL') {
        this.stationOptions = this.aelStations
      } else if (this.selectedLine == 'TCL') {
        this.stationOptions = this.tclStations
      } else if (this.selectedLine == 'WRL') {
        this.stationOptions = this.wrlStations
      } else {
        this.stationOptions = this.tklStations
      }

      this.selectedStation = this.stationOptions[0].value
      this.refresh()
    },
    refresh() {
      console.log('station changed')
      this.line = this.selectedLine
      this.sta = this.selectedStation
      this.refreshTables()
    },
    nextTrainTtntText(ttnt) {
      if (ttnt < 1) {
        return '正在離開...'
      }

      if (ttnt < 2) {
        return '即將到達...'
      }

      return `${ttnt}分鐘`
    },
    myProvider() {
      // Here we don't set isBusy prop, so busy state will be
      // handled by table itself
      return fetch(`https://rt.data.gov.hk/v1/transport/mtr/getSchedule.php?line=${this.line}&sta=${this.sta}`, {
        method: 'get'
      }).then((response) => {
        return response.json()
      }).then((jsonData) => {
        let items = jsonData.data[`${this.line}-${this.sta}`]['UP']
        console.log(items)
        // Here we could override the busy state, setting isBusy to false
        for (let i = 0; i < items.length; i++){
          items[i]['ttnt'] = this.nextTrainTtntText(items[i]['ttnt'])
          items[i]['dest'] = this.codeToText(items[i]['dest'])
          this.allUpDestinations[i] = (items[i]['dest'])
        }

        let uniqueItems = [...new Set(this.allUpDestinations)]
        this.uniqueUpDestinations = uniqueItems.join('/')

        this.results = jsonData['data'][`${this.line}-${this.sta}`]
        this.currentTime = jsonData['curr_time']
        return(items)
      }).catch(error => {
        // Here we could override the busy state, setting isBusy to false
        // Returning an empty array, allows table to correctly handle
        // internal busy state in case of error
        console.log(error)
        return []
      })
    },
    myProvider2() {
      // Here we don't set isBusy prop, so busy state will be
      // handled by table itself
      return fetch(`https://rt.data.gov.hk/v1/transport/mtr/getSchedule.php?line=${this.line}&sta=${this.sta}`, {
        method: 'get'
      }).then((response) => {
        return response.json()
      }).then((jsonData) => {
        let items = jsonData.data[`${this.line}-${this.sta}`]['DOWN']
        console.log(items)
        // Here we could override the busy state, setting isBusy to false
        // this.isBusy = false
        for (let i = 0; i < items.length; i++){
          items[i]['ttnt'] = this.nextTrainTtntText(items[i]['ttnt'])
          items[i]['dest'] = this.codeToText(items[i]['dest'])
          this.allDownDestinations[i] = (items[i]['dest'])
        }

        let uniqueItems = [...new Set(this.allDownDestinations)]
        this.uniqueDownDestinations = uniqueItems.join('/')
        return(items)
      }).catch(error => {
        // Here we could override the busy state, setting isBusy to false
        // Returning an empty array, allows table to correctly handle
        // internal busy state in case of error
        console.log(error)
        return []
      })
    },
    codeToText(code) {
      switch(code){
        // lines
        case 'AEL':
          return '機場快綫'
        case 'TCL':
          return '東涌綫'
        case 'WRL':
          return '西鐵綫'
        case 'TKL':
          return '將軍澳綫'

        // stations
        case 'HOK':
          return '香港'
        case 'KOW':
          return '九龍'
        case 'TSY':
          return '青衣'
        case 'AIR':
          return '機場'
        case 'AWE':
          return '博覽館'

        case 'OLY':
          return '奧運'
        case 'NAC':
          return '南昌'
        case 'LAK':
          return '荔景'
        case 'SUN':
          return '欣澳'
        case 'TUC':
          return '東涌'

        case 'HUH':
          return '紅磡'
        case 'ETS':
          return '尖東'
        case 'AUS':
          return '柯士甸'
        case 'MEF':
          return '美孚'
        case 'TWW':
          return '荃灣西'
        case 'KSR':
          return '錦上路'
        case 'YUL':
          return '元朗'
        case 'LOP':
          return '朗屏'
        case 'TIS':
          return '天水圍'
        case 'SIH':
          return '兆康'
        case 'TUM':
          return '屯門'

        case 'NOP':
          return '北角'
        case 'QUB':
          return '鰂魚涌'
        case 'YAT':
          return '油塘'
        case 'TIK':
          return '調景嶺'
        case 'TKO':
          return '將軍澳'
        case 'LHP':
          return '康城'
        case 'HAH':
          return '坑口'
        case 'POA':
          return '寶琳'
      }

      return ''
    },
    refreshTables() {
      this.allUpDestinations = []
      this.allDownDestinations = []
      this.$refs.table.refresh();
      this.$refs.table2.refresh();
    }
  },
  mounted: function() {
    this.stationOptions = this.wrlStations
  },
  props: {
  }
}
</script>

